import { Store, useStore } from "./Store";

// export const authStore = new Store<null | string>(typeof localStorage === "undefined" ? null : localStorage['jwt']);

export const LanguageStore = new Store<{ key: string, value: string }>({ key: "ENGLISH", value: "ENGLISH" });


export const useLanguageStore = () => {
    const [Language, setLanguage] = useStore(LanguageStore);

    return {
        Language, setLanguage,

    }
};