import { Store, useStore } from "./Store";
import getGamedatalog from "../utils/api/getGamedatalog";

export class GamedatalogItem {

    id: String;
    level_sublevel_id: String;
    level_sublevel: String;
    data: String;
    misc: String;
    status: String;
    created_at: String;
    updated_at: String;
    user: any;
    instance: any;

    constructor(o: {
        id: String;
        level_sublevel_id: String;
        level_sublevel: String;
        data: String;
        misc: String;
        status: String;
        created_at: String;
        updated_at: String;
        user: any;
        instance: any

    }) {
        this.id = o.id;
        this.level_sublevel_id = o.level_sublevel_id;
        this.level_sublevel = o.level_sublevel;
        this.data = o.data;
        this.misc = o.misc;
        this.status = o.status;
        this.created_at = o.created_at;
        this.updated_at = o.updated_at;
        this.user = o.user;
        this.instance = o.instance;
    }
}

export const GamedatalogStore = new Store<GamedatalogItem[] | false>(false);

export const Gamedatastore = () => {
    const [Gamedatalog, setGamedatalog] = useStore(GamedatalogStore);

    return {
        Gamedatalog, setGamedatalog,

        async loadGamedatalog() {
            await getGamedatalog("", "").then(
                (data) => {
                    console.log(">>> data.data.resultsdata.data.results :", data.data);
                    setGamedatalog(data.data);
                }
            )
        },
        async loadGamedatalogfilter(level: any, user: any) {
            await getGamedatalog(level, user).then(
                (data) => {
                    console.log(">>> data.data.resultsdata.data.results :", data.data);
                    setGamedatalog(data.data);
                }
            )
        },
    }
}