import { Store, useStore } from "./Store";
import getleveldetailsbyid from "../utils/api/getleveldetailsbyid";
import getleveldetailsbyqrid from "../utils/api/getleveldetailsbyqrid";


export class leveldetailsItem {

    id: String;
    l_level: String;
    l_icon: String;
    l_qrcode: String;
    l_title: String;
    l_stitle: String;
    l_body: String;
    l_bodytype: String;
    l_bodyoptions: String;
    locations: boolean;
    l_media: String;
    l_misc: String;
    created_at: String;
    updated_at: String;
    instance_id: String;
    SubLevelData: String;
    GameDataLog: any;
    progress: Number;
    TextData: any;
    completion_image: any;

    constructor(o: {
        id: String;
        l_level: String;
        l_icon: String;
        l_qrcode: String;
        l_title: String;
        l_stitle: String;
        l_body: String;
        l_bodytype: String;
        l_bodyoptions: String;
        locations: boolean;
        l_media: String;
        l_misc: String;
        created_at: String;
        updated_at: String;
        instance_id: String;
        SubLevelData: any;
        GameDataLog: any;
        progress: Number;
        TextData: any;
        completion_image: any;


    }) {
        this.id = o.id;
        this.l_level = o.l_level;
        this.l_icon = o.l_icon;
        this.l_qrcode = o.l_qrcode;
        this.l_title = o.l_title;
        this.l_stitle = o.l_stitle;
        this.l_body = o.l_body;
        this.l_bodytype = o.l_bodytype;
        this.l_bodyoptions = o.l_bodyoptions;
        this.locations = o.locations;
        this.l_media = o.l_media;
        this.l_misc = o.l_misc;
        this.created_at = o.created_at;
        this.updated_at = o.updated_at;
        this.instance_id = o.instance_id;
        this.SubLevelData = o.SubLevelData;
        this.GameDataLog = o.GameDataLog;
        this.progress = o.progress;
        this.TextData = o.TextData
        this.completion_image = o.completion_image

    }
}

export const leveldetailsStore = new Store<leveldetailsItem | false>(false);

export const userActiveLevelDetailsStore = () => {
    const [ActiveLevelDetails, setActiveLevelDetails] = useStore(leveldetailsStore);

    return {
        ActiveLevelDetails, setActiveLevelDetails,

        async loadActiveleveldetails(id: any) {
            await getleveldetailsbyid(id).then(
                (data) => {
                    // console.log("data of level ", data.data.results[0]);
                    setActiveLevelDetails(data.data.results[0]);
                }
            )
        },
        async loadActiveleveldetailsbyqr(id: any) {
            await getleveldetailsbyqrid(id).then(
                (data) => {
                    setActiveLevelDetails(data.data.results[0]);
                }
            )
        }
    }
}