import { Store, useStore } from "./Store";

// export const authStore = new Store<null | string>(typeof localStorage === "undefined" ? null : localStorage['jwt']);

export const ActiveInstance1 = new Store<Number>(-1);

export const useActiveInstance = () => {

    const [ActiveInstance, setActiveInstance] = useStore(ActiveInstance1);

    return {
        ActiveInstance, setActiveInstance,
    }
};